<template>
    <div :style="'background:url('+bg+') no-repeat;background-size:cover;overflow:hidden;'">
        <h1 class="main-title">最近上映</h1>
        <Row>
            <Col class="index-list" v-for="(item,index) in nowList" :key="item.id" :xs="{ span: 24, offset:0 }" :lg="{ span: 6, offset: index%3===0?0:3 }">
            <img class="film-img" :src="item.picSrc | imagePath">
            <h3>{{item.title}}<Button @click="toBook(item)" class="index-list-btn" type="primary">订票</Button></h3>
            </Col>
        </Row>
        <h1 class="main-title">即将上映</h1>
        <Row>
            <Col class="index-list" v-for="(item,index) in lateList" :key="item.id" :xs="{ span: 24, offset:0 }" :lg="{ span: 6, offset: index%3===0?0:3 }">
            <img class="film-img" :src="item.picSrc | imagePath">
            <h3>{{item.title}}<Button @click="toBook(item)" class="index-list-btn" type="primary">订票</Button></h3>
            </Col>
        </Row>
        <h1 class="main-title">热播影片</h1>
        <Row>
            <Col class="index-list" v-for="(item,index) in hotList" :key="item.id" :xs="{ span: 24, offset:0 }" :lg="{ span: 6, offset: index%3===0?0:3 }">
            <img class="film-img" :src="item.picSrc | imagePath">
            <h3>{{item.title}}<Button @click="toBook(item)" class="index-list-btn" type="primary">订票</Button></h3>
            </Col>
        </Row>
    </div>
</template>

<script>
    import requestObj from '../../api/api';
    export default {
        name: "scenic-spot",
        data(){
            return {
            	bg:require('../../assets/frontend/s-bg.jpg'),
                nowList:[],
                lateList:[],
                hotList:[],
            };
        },
        created(){
            this.initNowData();
            this.initLateData();
            this.initHotData();
        },
        methods:{
            initNowData(){
                var self=this;
                requestObj({
                    url:'getFilmPageInfo.do',
                    method:'GET',
                    params:{
                        pageCurrent:1,
                        pageSize:10,
                        bigtype:'正在上映',
                    },
                    success:function (res){
                        var data=res.data.data.rows;
                        self.nowList=data;
                    },
                    fail:function (err){
                        console.log(err);
                    },
                });
            },
            initLateData(){
                var self=this;
                requestObj({
                    url:'getFilmPageInfo.do',
                    method:'GET',
                    params:{
                        pageCurrent:1,
                        pageSize:10,
                        bigtype:'即将上映',
                    },
                    success:function (res){
                        var data=res.data.data.rows;
                        self.lateList=data;
                    },
                    fail:function (err){
                        console.log(err);
                    },
                });
            },
            initHotData(){
                var self=this;
                requestObj({
                    url:'getFilmPageInfo.do',
                    method:'GET',
                    params:{
                        pageCurrent:1,
                        pageSize:10,
                        bigtype:'热播影片',
                    },
                    success:function (res){
                        var data=res.data.data.rows;
                        self.hotList=data;
                    },
                    fail:function (err){
                        console.log(err);
                    },
                });
            },
	        toBook(item){
		        this.$router.push('/book/'+item.id);
	        },
        },
    }
</script>

<style scoped>
    .film-img{
        width:100%;
        height:200px;
    }
</style>


